import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Wrapper = styled.div`

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  padding: 0px 2.5px;
  background: #33b0d6;

  @media(max-width: 768px){
    overflow-x: scroll;
    overflow-y  : hidden;
  }

  nav{
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    a{
      text-decoration: none;
      color: white;
      padding: 5px 10px;
      &:hover{
        color: #d4d4d4;
      }
    }
  }
`;

const Navbar = () => {
  return (
    <Wrapper>
      <nav>
        <Link to='/'> Main </Link>
        <Link to='/docs'> Documentation </Link>
        <Link to='/examples'> Examples </Link>
        <Link to='/projects'> Projects </Link>
      </nav>
      <div>
      FB - NPM 
      </div>
    </Wrapper>
  )
}

export default Navbar